body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100%;
  min-height: 100vh;
}

input.input-author {
  border: none;
  border-bottom: 0.5px solid #aaa;
  width: 20%;
  margin-bottom: 10px;
  font-family: inherit;
  font-size: 1em;
  padding-left: 5px;
}

input.input-author:focus,
.page-size-select select:focus {
  outline: none;
  border-bottom: 0.5px solid #777;
}

.fa-search {
  color: #aaa;
  font-size: 1em;
}

.page-size-select {
  float: right;
  margin-left: 20px;
  color: #777;
  font-family: inherit;
}

.page-size-select select {
  border: none;
  border-bottom: 0.5px solid #aaa;
  font-family: inherit;
  font-size: 1em;
}

.pagination {
  text-align: center;
  padding: 10px;
  color: #777;
}

.pagination ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.pagination ul li {
  margin: 5px;
  cursor: pointer;
}

.pagination ul li:hover {
  color: #222;
}

.pagination ul li.active {
  color: #222;
  text-decoration: underline;
}

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
}

.gallery {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.photo-container-div {
  margin: 10px;
}

.photo-div {
  position: relative;
}

.load-icon-div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.load-icon-div i.fa-spinner {
  position: absolute;
  font-size: 2em;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.photo-div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.image-div {
  transition: 0.5s ease;
}

.image-div:hover {
  opacity: 0.5;
}

.zoom-icon-div {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s ease;
}

.zoom-icon-div:hover {
  z-index: 1;
}

.zoom-icon-div i.fa-search-plus {
  position: absolute;
  font-size: 2em;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.author-div {
  text-align: center;
}

.author-div p {
  margin: 0;
  font-size: 14px;
  color: #aaa;
  opacity: 0;
  transition: 0.5s ease;
}

.author-div p:hover {
  color: #000;
}

@media (max-width: 340px) {
  .photo-container-div {
    width: 90%;
  }

  .photo-div img {
    width: 100%;
  }
}

footer {
  width: 100%;
  box-sizing: border-box;
  padding: 15px;
  text-align: center;
  border-top: 1px #aaa solid;
  font-size: 16px;
  color: #777;
}

footer span {
  margin-right: 2px;
}

.selected-photo {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.selected-photo i {
  font-size: 3em;
  margin: 10px;
}

.selected-photo i.fa-spinner {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0;
}

.selected-photo i.fa-long-arrow-left {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 2em;
}

.selected-photo img {
  max-width: 80vw;
  max-height: 95vh;
}

a {
  text-decoration: none;
  color: #333;
}

a:hover {
  text-decoration: none;
  color: #000;
}

.to-gallery-div {
  width: 140px;
}

.to-gallery-div i {
  padding: 4px;
  float: left;
}

.to-gallery-div p {
  margin: 0;
  float: left;
}

